
const SendApi = (collection, invoiceInfo, response) => {
      const docType = () => {
            if(invoiceInfo.docType === 'dni'){
                  return 1;
             }
             if(invoiceInfo.docType === 'ruc'){
                  return 6;
            }
            return 0;
      }
      const type=()=>{
         if(invoiceInfo.type === 'invoice'){
            return '01';
         }
         if(invoiceInfo.type === 'ticket'){
            return '03';
         }
         if(invoiceInfo.type === 'note'){
            return '77';
         }
      }
      function calcTime(offset) {
         // create Date object for current location
         var d = new Date();
     
         // convert to msec
         // subtract local time zone offset 
         // get UTC time in msec
         var utc = d.getTime() + (d.getTimezoneOffset() * 60000);
     
         // create new Date object for different city
         // using supplied offset
         var nd = new Date(utc + (3600000*offset));
         return nd;
         // // return time as a string
         // return "The local time for city"+ city +" is "+ nd.toLocaleString();
     }
     
      const data = 
      {
          "contribuyente":{
             "token_contribuyente": response.token? response.token: "SC4H9H0U2TXE1NYU4P0KRJIETM2QZ1MMYMM9X",// token is very important.
             "id_usuario_vendedor": response.vendor ? response.vendor : 0 , // default 0
             "tipo_proceso": response.process ? response.process : 'prueba', //default prueba.
             "tipo_envio":"inmediato"
          },
          "cliente":{
             "tipo_docidentidad": docType(), // 1: dni, 6: ruc, another is 0.
             "numerodocumento": invoiceInfo.doc, //
             "nombre":  invoiceInfo.name, //
             "email":"",
             "direccion": invoiceInfo.direction, //
             "ubigeo":"",
             "sexo":"",
             "fecha_nac":"",
             "celular":""
          },
          "cabecera_comprobante":{
             "tipo_documento": type(), // 01: invoice or 03: ticket or 77: note
             "moneda":"PEN",
             "idsucursal": response.branch ? response.branch : 23, // default 23.
             "id_condicionpago":"0",
             "fecha_comprobante": `${calcTime('-5').getDate()}/${calcTime('-5').getMonth()+1}/${calcTime('-5').getFullYear()}`,
             "nro_placa":"",
             "nro_orden":"",
             "guia_remision":"",
             "descuento_monto":0,
             "observacion":"",
             "descuento_porcentaje": ""
          },
          "detalle":[
            //  {
            //     "idproducto":0,
            //     "codigo":"TV_CODIGOPROD",
            //     "afecto_icbper":"no",
            //     "id_tipoafectacionigv":10,
            //     "descripcion":"Product 01", //
            //     "idunidadmedida":"NIU",
            //     "precio_venta":500, //
            //     "cantidad":2 //
            //  },
          ]
      }
      // eslint-disable-next-line array-callback-return
      collection.map((el, index)=>{
         if(el.index === index){
            // eslint-disable-next-line no-mixed-operators
            if((el.count === 1) && (el.amount === '0') || (el.amount === '')){
            	//don't include
            }else{
               data.detalle.push({
                  "idproducto":0,
                  "codigo":"000",
                  "afecto_icbper":"no",
                  "id_tipoafectacionigv":10,
                  "descripcion" : el.description,
                  "idunidadmedida":"NIU",
                  "precio_venta" : el.amount,
                  "cantidad": el.count
            })
            }
            
         }
      })
      console.log(data)
      const postDetail = {
         method: 'POST',
         headers: {
            "Content-Type": "application/json",
         },
         body: JSON.stringify(data)
      }
      return postDetail;
}

export default SendApi;